const initState = {
    authError: null
};

const authReducer = (state = initState, action) => {
    switch(action.type){
        case "LOGIN_ERROR":
            console.log("ðŸŒ :(")
            return {
                ...state, 
                authError: "Login failed"
            }
        case "LOGIN_SUCCESS":
            console.log("ðŸŒ :)")
            return {
                ...state,
                authError: null
            }
        case "SIGNOUT_SUCCESS":
            console.log("ðŸŒðŸŒðŸŒ :)")
            return state
        case "SIGNUP_SUCCESS":
            console.log("ðŸŒðŸŒðŸŒ :)")
            return {
                ...state,
                authError: null
            }
        case "SIGNUP_ERROR":
            console.log("ðŸŒðŸŒðŸŒ :(")
            return {
                ...state,
                authError: action.err.message
            } 
        default:
            return state;
    }
};

export default authReducer